import * as React from 'react';
import { Radio, RadioGroup, theme, ToolbarDeleteLinkNormal } from '@ali/we-design';
import { Controller } from '@ali/4ever-cangjie';
import {
  ItemInput,
  SaveButton,
  RemoveButton,
  EditorWrapper,
  EditorContent,
  EditorFooter,
  RadioWrapper,
  ItemRadio,
} from './styled';
import { ThemeProvider } from 'styled-components';

import { LinkPluginConfig, LinkDisplayType } from '../types';
import { focusLinkEditor, hideLinkEditor, hideLinkEditorAndMoveToNextText } from '../actions';
import { useOnClickOutside } from '@ali/4ever-component';
import { isJustCreated, setJustCreated } from '../utils/linkStore';

function stopPropagation(event) {
  event.stopPropagation();
}

const isLinkDisplayType = (value: string): value is LinkDisplayType => {
  return value === 'link' || value === 'card';
};

interface LinkEditorProps {
  nodeKey?: string;
  href: string;
  text: string;
  displayType?: LinkDisplayType;
  textInputRef?: React.RefObject<HTMLInputElement>;
  hrefInputRef?: React.RefObject<HTMLInputElement>;
  controller: Controller;
  locale: LinkPluginConfig['locale'];
  onHide: () => void;
  onSave: (text: string, href: string, displayType: LinkDisplayType) => void;
  onRemove: () => void;
  onUnlink: () => void;
  onClickoutside: (event: MouseEvent) => void;
  // 仅将 text 作为 placeholder 显示
  showPlaceholder?: boolean;
  // todo 全量后下线配置
  enableCard: boolean;
}

const LinkEditor = React.forwardRef<HTMLDivElement, LinkEditorProps>(
  (props, ref) => {
    const {
      nodeKey,
      href: propsHref,
      text: propsText,
      displayType: propsDisplayType = 'link',
      locale,
      controller,
      onSave,
      onUnlink,
      onHide,
      onClickoutside,
      enableCard,
      showPlaceholder,
      textInputRef: textInput,
      hrefInputRef: hrefInput,
    } = props;

    const [href, setHref] = React.useState(propsHref);
    const [text, setText] = React.useState(propsText);
    const [displayType, setDisplayType] = React.useState<LinkDisplayType>(propsDisplayType);

    const inputFocused = React.useRef(false);

    React.useEffect(() => {
      if (propsHref) return;
      if (!nodeKey) return; // pureLink
      // 这个地方 node 的 key 可能会是 viewPack 的 key
      // 在调用 setJustCreated 的地方使用的 dataPack 中的 key
      // 因此取一下 dataPack 的 key
      const dataPackKey = nodeKey.split('-')[0];
      if (!isJustCreated(dataPackKey)) return;
      setJustCreated(dataPackKey, false);
      // 如果是占位符，直接 focus 标题输入框
      if (showPlaceholder && textInput?.current) {
        textInput.current.focus();
        return;
      }
      if (hrefInput?.current) hrefInput.current.focus();
    }, [propsHref, nodeKey, showPlaceholder]);
    useOnClickOutside(ref as React.RefObject<HTMLDivElement>, onClickoutside);

    React.useEffect(() => {
      setText(propsText);
      setHref(propsHref);
    }, [propsText, propsHref]);

    const hide = React.useCallback(() => {
      onHide();
      if (showPlaceholder) {
        // 如果是占位符点击出现的编辑框，使用 esc 退出时，将光标移动到占位符外部
        controller.run('onAction', hideLinkEditorAndMoveToNextText());
      } else {
        controller.run('onAction', hideLinkEditor());
      }
    }, [controller, onHide, showPlaceholder]);

    const save = React.useCallback(() => {
      const trimedText = text.trim();
      const trimedHref = href.trim();

      if (trimedText.length || trimedHref.length) {
        onSave(trimedText, trimedHref, displayType || propsDisplayType);
      }
      onHide();
    }, [text, href, displayType, propsDisplayType, onSave, onHide]);

    const handleKeyDown = React.useCallback((event: React.KeyboardEvent) => {
      if (!event.key) {
        return;
      }

      // 防止 keydown 透出到编辑器
      event.stopPropagation();

      const key = event.key.toLowerCase();
      // React event 没有这个属性，要到原生 event 上取
      // https://github.com/facebook/react/issues/13104
      if (event.nativeEvent.isComposing) {
        return;
      }
      if (key.toLowerCase() === 'escape' || event.keyCode === 27) {
        event.preventDefault();
        // ESC 退出
        hide();
      } else if (
        (key === 'enter' || event.keyCode === 13) &&
        inputFocused.current
      ) {
        // Enter 保存
        event.preventDefault();
        save();
      }
    }, [hide, save]);

    const handleHrefChange = React.useCallback(
      (e: React.ChangeEvent<HTMLInputElement>) => {
        setHref(e.target.value);
      },
      [],
    );

    const handleTextChange = React.useCallback(
      (e: React.ChangeEvent<HTMLInputElement>) => {
        setText(e.target.value);
      },
      [],
    );

    const handleSave = React.useCallback(
      (event: React.MouseEvent) => {
        event.preventDefault();
        event.stopPropagation();
        save();
      },
      [save],
    );

    const handleUnlink = React.useCallback(
      (event: React.MouseEvent) => {
        event.preventDefault();
        event.stopPropagation();
        onUnlink();
      },
      [onUnlink],
    );

    const handleFocus = React.useCallback(
      (event: React.FocusEvent) => {
        event.preventDefault();
        event.stopPropagation();
        inputFocused.current = true;
        controller.run('onAction', focusLinkEditor());
      },
      [controller],
    );

    const handleBlur = React.useCallback(() => {
      inputFocused.current = false;
    }, []);

    const handleClick = React.useCallback((event: React.MouseEvent) => event.stopPropagation(), []);

    const handleRadioClick = React.useCallback(
      (e: string) => {
        if (isLinkDisplayType(e)) {
          setDisplayType(e);
        }
      }, [],
    );

    const saveButtonDisbaled = React.useMemo(() => {
      const trimedHref = href.trim();
      const trimedText = text.trim();
      const unchanged = trimedHref === propsHref.trim()
        && trimedText === propsText.trim()
        && propsDisplayType === displayType;
      // 当「链接」无内容或者未编辑时，不允许保存
      return trimedHref.length === 0 || unchanged;
    }, [href, text, displayType, propsHref, propsText, propsDisplayType]);

    return (
      <EditorWrapper onClick={handleClick} data-testid="link-editor" ref={ref} onKeyDown={handleKeyDown}>
        <EditorContent>
          <ItemInput>
            <label htmlFor="text">{locale.editText}</label>
            <input
              name="text"
              type="text"
              value={text}
              ref={textInput}
              placeholder={locale.editTextPlaceholder}
              onBlur={handleBlur}
              onChange={handleTextChange}
              onFocus={handleFocus}
              onMouseDown={stopPropagation}
              autoComplete="off"
              data-testid="link-editor-input-text"
            />
          </ItemInput>
          <ItemInput>
            <label htmlFor="href">{locale.editHref}</label>
            <input
              name="href"
              type="text"
              value={href}
              ref={hrefInput}
              placeholder={locale.editHrefPlaceholder}
              onBlur={handleBlur}
              onChange={handleHrefChange}
              onFocus={handleFocus}
              onMouseDown={stopPropagation}
              autoComplete="off"
              data-testid="link-editor-input-href"
            />
          </ItemInput>
          {
            enableCard &&
            <ItemRadio>
              <label htmlFor="displayType">{locale.editStyle}</label>
              <RadioWrapper>
                <RadioGroup onChange={handleRadioClick} value={displayType} >
                  <ThemeProvider theme={{ ui: { ...theme, padding: '8px' } }}>
                    <Radio value="link">
                      {locale.link}
                    </Radio>
                    <Radio value="card">
                      {locale.editStyleCard}
                    </Radio>
                  </ThemeProvider>
                </RadioGroup>
              </RadioWrapper>
            </ItemRadio>
          }
        </EditorContent>
        <EditorFooter>
          <RemoveButton
            onMouseDown={handleUnlink}
            data-testid="link-editor-unlink"
          >
            <ToolbarDeleteLinkNormal style={{ marginRight: 4 }} />
            <span className="text">{locale.unlink}</span>
          </RemoveButton>
          <SaveButton
            type={'primary'}
            onMouseDown={handleSave}
            testid="link-editor-save"
            disabled={saveButtonDisbaled}>
            <span className="text">{locale.save}</span>
          </SaveButton>
        </EditorFooter>
      </EditorWrapper>
    );
  },
);

export default React.memo(LinkEditor);
